import randomItem from 'random-item';

export const winMessage = (n: number) => {
    switch(n) {
        case 1:
            return randomItem(['NGULO!', 'Alla caccia del fazzo', `L'uno  la solitudine`, ` c[] `, '\o']);

        case 2:
            return randomItem(['Quasi geni!', 'Geni o fortunell?', 'Brav nonch breve', `()`, `(.  .)`, `' '\\=(_)=//'' `, '(`._.(`._. super ._.)._.)', '**--**--']);

        case 3:
            return randomItem(['Praeclare!', '\\( )/', '()', '\,,/(^^)\,,/', `||`, '(/\)', `(_)`, '(^^)', '', '()()', 'La fortuna del 3 non  opera del diavolo']);

        case 4:
            return randomItem(['', '', '', `@('_')@`, ``, `(   )`, `()`]);

        case 5:
            return randomItem(['Poteva andare peggio!', 'Piano ma lontano', `(_)`, ``, `(.)`, '@( * O * )@', '', '(   )', `\\\\`, `\\(/.__.)/  \\(.__.\\)`]);

        default:
            return randomItem([`()`, 'Fiuuuuuuuu!', `()`, '( [_]||', `^(;,;)^`]) || 'Fiuuuuuuuu!';
    }
}

